<template>
    <div id="app">
        <router-view v-if="isRouterAlive" v-wechat-title='$store.state.merchant.title'></router-view>
    </div>
</template>

<script>
export default {
  name: 'App',
    provide () {
        return {
            reload: this.reload
        }
    },
    data () {
        return {
            isRouterAlive: true 
        }
    },
    methods: {
        reload () {
            this.isRouterAlive = false
            this.$nextTick(function () {
                this.isRouterAlive = true
            })
        },
    },
    created() {
    }
}
</script>

<style>

</style>



